.reviews {
  overflow: hidden;
}

.reviews__item {
  display: flex;
  justify-content: center;
  font-size: 0;
  padding-top: 60px;
  padding-bottom: 50px;
}

.reviews__img {
  position: relative;
  background: @colorGreenLight;
  padding: 0 15px;
  height: 140px;

  &::after {
    content: '';
    display: block;
    background: @colorGreenLight;
    width: 50vw;
    height: 140px;
    position: absolute;
    top: 0;
    right: 100%;
  }
}
/*.reviews__item .reviews__img::after*/
.reviews__img--right::after {
  right: 0;
  left: 100%;
}

.reviews__imgItem {
  width: 180px;
  margin-top: -20px;
  position: relative;
  z-index: 5;
}

.reviews__author {
  width: 180px;
  text-align: center;
}

.reviews__name {
  font-size: 16px;
  line-height: 16px;
  font-weight: 600;
  display: block;
  margin-top: 25px;
  margin-bottom: 5px;
}

.reviews__contact {
  font-size: 14px;
  line-height: 14px;
}

.reviews__text {
  font-size: 16px;
  line-height: 20px;
  max-width: 485px;
  margin-top: -20px;
  margin-bottom: 0;
  margin-left: 80px;
  position: relative;

  &:after {
    content: url("../img/icon-quote.svg");
    display: block;
    position: absolute;
    top: -35px;
    left: -70px;
  }
}

.reviews__text--left {
  order: -1;
  margin-left: 0;
  margin-right: 60px;
}

@media screen and (max-width: 800px) {
  .reviews {
    padding: 0 20px;
  }

  .reviews__item {
    flex-direction: column;
    align-items: center;
  }

  .reviews__text {
    margin-top: 20px;
    margin-left: 0;
  }

  .reviews__text--left {
    order: 1;
    margin-right: 0;
  }
}
