.modalRequest {
  width: 651px;
  padding: 40px 20px 30px 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  background-color: #ffffff;
  text-align: center;
  display: none;

  &::after {
    content: '';
    width: 100%;
    height: 100%;
    border: 15px solid rgba(129, 216, 208, 0.8);
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.modalHead {
  font-size: 28px;
  font-weight: 600;
  line-height: 28px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 10px;

}

.modalRequest__description {
  font-size: 18px;
  line-height: 18px;
  color: #a59552;
  margin-top: 0;
  margin-bottom: 30px;
}

.modalRequest__formWrap {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 30px;

  &::after {
    content: "";
    background-image: url("../img/icon-img-logo.png");
    background-size: cover;
    width: 100px;
    height: 112px;
    position: absolute;
    top: 30px;
    left: 30px;
    z-index: 5;
  }

  &::before {
    content: "";
    background-image: url("../img/icon-img-logo.png");
    background-size: cover;
    width: 100px;
    height: 112px;
    position: absolute;
    bottom: 30px;
    right: 30px;
  }
}

.modalRequest__formItem {
  margin: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.modalRequest__input {
  width: 255px;
  padding: 15px 0 15px 45px;
  margin-bottom: 20px;
}

.modalRequest__element {
  position: relative;

  &::after {
    content: url("../img/icon-user.svg");
    position: absolute;
    top: 19px;
    left: 15px;
  }
}

.modalRequest__element--phone {
  &::after {
    content: url("../img/icon-phone-green.svg");
  }
}

.modalRequest__element--mail {
  &::after {
    content: url("../img/icon-mail-green.svg");
    top: 17px;
  }
}

.modalRequest__element--file {
  &::after {
    content: url("../img/icon-download.svg");
    top: 2px;
  }
}

.modalRequest__file {
  display: block;
  padding-left: 45px;
  color: #a59552;
}

.modalRequest__labelText {
  display: block;
  margin-bottom: 10px;
}

.modalRequest__text {
  width: 255px;
  height: 167px;
  padding-top: 20px;
  padding-left: 20px;
  resize: none;
}

@media screen and (max-width: 680px) {
  .modalRequest {
    width: auto;
  }

  .modalRequest__formWrap {

    &::before {
      display: none;
    }
  }

  .modalRequest__text,
  .modalRequest__labelText,
  .modalRequest__element--mail,
  .modalRequest__element--file,
  .modalRequest__file {
    display: none;
  }
}
