.modalLuck {
  width: 590px;
  padding: 40px 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  background-color: #ffffff;
  text-align: center;
  display: none;

  &::after {
    content: '';
    width: 100%;
    height: 100%;
    border: 15px solid rgba(129, 216, 208, 0.8);
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.modalLuck__description {
  line-height: 16px;
}

.modalLuck__text {
  line-height: 20px;
}

.modalLuck__link {
  font-weight: 600;
  color: #a59552;
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .modalLuck {
    width: 300px;
  }
}
