.button {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  color: #a59552;
  background-color: transparent;
  border: 1px solid #a59552;
  padding: 10px 20px;
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    background-color: @colorGreen;
    border-color: @colorGreen;
    color: #ffffff;
  }
}

.button--projectWork {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  padding: 10px 40px;
}

.buttonClosed {
  position: absolute;
  top: -11px;
  right: -11px;
  border: none;
  background: transparent;
  cursor: pointer;
  z-index: 5;
}
