.projectWork {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.projectWork__img {
  position: relative;
  background: @colorGreenLight;
  padding: 0 15px;
  font-size: 0;
  align-self: flex-start;

  &::after {
    content: '';
    display: block;
    background: @colorGreenLight;
    width: 50vw;
    height: 258px;
    position: absolute;
    top: 0;
    right: 100%;
  }
}

.projectWork__listHead {
  margin-top: 0;
  margin-bottom: 30px;
  margin-left: 20px;
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  padding: 0 10px 3px 2px;
  border-bottom: 2px solid @colorGreen;
  display: inline-block;
}

.projectWork__list {
  list-style-type: none;
  padding-left: 0;
  padding-right: 20px;
  margin-top: 0;
  margin-left: 45px;
}

.projectWork__listItem {
  padding-left: 25px;
  font-size: 18px;
  line-height: 18px;
  margin-bottom: 10px;
  position: relative;

  &::after {
    content: '';
    width: 7px;
    height: 7px;
    background: @colorGreen;
    border-radius: 50%;
    position: absolute;
    top: 6px;
    left: 0;
  }
}

@media screen and (max-width: 720px) {
  .projectWork {
    flex-direction: column;
    align-items: center;
    padding: 0 20px;
  }

  .projectWork__list {
    margin-top: 30px;
    margin-bottom: 0;
    margin-left: 0;
    padding: 0;
  }

  .projectWork__img {
    align-self: auto;
  }
}

@media screen and (max-width: 450px) {
  .projectWork__img::after {
    display: none;
  }
}
