.pagination {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 60px;
  border: 1px solid #cccccc;
}

.pagination__wrap {
  display: flex;
  justify-content: center;
}

.pagination__arrow {
  color: @colorGreen;
}

.pagination__arrow--notActive {
  color: #cccccc;
  cursor: text;
}

.pagination__arrow,
.pagination__number {
  font-size: 16px;
  line-height: 16px;
  width: 50px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.pagination__number:hover {
  color: #ffffff;
  background: @colorGreen;
}

@media screen and (max-width: 480px) {
  .pagination a:nth-child(n+4):nth-child(-n+6) {
    display: none;
  }
}
