.gallery {
  margin-bottom: 50px;
  font-size: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gallery__main {
  margin-bottom: 5px;
  padding-bottom: 10px;
  border-bottom: 1px solid @colorGreen;
}

.gallery__items {
  display: flex;
  flex-wrap: wrap;
  align-self: center;
  margin-right: -5px;
  margin-left: -5px;
}

.gallery__item {
  width: calc(25% - 10px);
  margin: 5px;
}

.gallery__main,
.gallery__item {
  cursor: pointer;
}

@media screen and (max-width: 900px) {
  .gallery__items {
    margin: 0 auto;
  }

  .gallery__item {
    width: calc(12% - 10px);
  }
}

@media screen and (max-width: 650px) {
  .gallery__item {
    width: calc(17% - 10px);
  }
}

@media screen and (max-width: 500px) {
  .gallery__item {
    width: calc(25% - 10px);
  }
}
