.blog {
  padding-left: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 40px;
  margin-right: -10px;
  margin-left: -10px;
}

.blog__item {
  width: calc(33.3% - 20px);
  border: 1px solid @colorGreen;
  margin-right: 10px;
  margin-bottom: 30px;
  margin-left: 10px;
}

.blog__img {
  position: relative;

  &::after {
    content: '';
    width: 100%;
    height: 100%;
    border: 15px solid rgba(129, 216, 208, 0.8);
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.blog__text {
  padding: 0 20px 10px 20px;
}

.blog__head {
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  text-align: center;
  margin-top: 22px;
}

.blog__description {
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 20px;
}

.blog__link {
  font-size: 14px;
  text-transform: uppercase;
  text-align: right;
  color: #a59552;
  display: block;
  padding: 10px 0 10px 10px;
  transition: 0.3s;

  &:hover {
    color: @colorGreen;
  }
}

@media screen and (max-width: 880px) {
  .blog {
    padding: 0 20px;
  }

  .blog__item {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 580px) {
  .blog {
    justify-content: center;
  }

  .blog__item {
    width: 75%;
  }
}

@media screen and (max-width: 400px) {
  .blog__item {
    width: auto;
  }
}
