.services {
  overflow: hidden;
}

.services__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.services__img {
  position: relative;
  background: @colorGreenLight;
  padding: 0 15px;
  font-size: 0;

  &::after {
    content: '';
    display: block;
    background: @colorGreenLight;
    width: 50vw;
    height: 210px;
    position: absolute;
    top: 0;
    right: 100%;
  }
}

.services__img--right {
  order: 1;

  &::after {
    right: 0;
    left: 100%;
  }
}

.services__name {
  margin-left: 50px;
  width: 380px;
}

.services__name--left {
  margin-left: 0;
  margin-right: 50px;
  text-align: right;
}

.services__head {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 30px;
  text-transform: uppercase;
}

.services__text {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 50px;
}

@media screen and (max-width: 790px) {
  .services__item {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
  }

  .services__name {
    width: auto;
    margin-left: 0;
    text-align: center;
  }

  .services__name .button {
    display: block;
    margin: 0 auto;
    width: 140px;
  }

  .services__img--right {
    order: 0;
  }

  .services__name--left {
    margin-right: 0;
  }
}

@media screen and (max-width: 510px) {
  .services__item,
  .services__text {
    padding: 0 20px;
  }
}

@media screen and (max-width: 400px) {
  .services__img::after {
    display: none;
  }
}
