.portfolio {
  padding: 0 20px;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 -10px 10px -10px;
}

.portfolio__item {
  width: calc(33.3% - 20px);
  font-size: 0;
  margin: 0 10px 20px 10px;
  background: @colorGreenLight;
}

.portfolio__link:hover > .portfolio__img::after {
  opacity: 1;
}

.portfolio__img {
  position: relative;

  &::after {
    content: '';
    width: 100%;
    height: 100%;
    border: 15px solid rgba(129, 216, 208, 0.8);
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: 0.3s;
  }
}

.portfolio__address {
  font-size: 18px;
  text-align: center;
  width: 240px;
  padding: 10px 0;
  margin: 0 auto;
}

@media screen and (max-width: 880px) {
  .portfolio__item {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 580px) {
  .portfolio {
    justify-content: center;
  }

  .portfolio__item {
    width: 75%;
  }
}

@media screen and (max-width: 400px) {
  .portfolio__item {
    width: auto;
  }
}
