.portfolioProject {
  display: flex;/*
  flex-wrap: wrap;*/
  margin: 50px 0;
  padding: 0 20px;
}

.portfolioProject__media {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portfolioProject__text {
  width: calc(70% - 50px);
  margin-left: 50px;
  font-size: 18px;
  line-height: 28px;
}

.portfolioProject__text p {
  margin-top: 0;
}

@media screen and (max-width: 900px) {
  .portfolioProject {
    flex-direction: column;
  }

  .portfolioProject__media {
    width: auto;
  }

  .portfolioProject__text {
    width: auto;
    margin-top: 30px;
    margin-left: 0;
  }
}
