@font-face {
  font-family: "Lato";
  src: url("../fonts/latolight.woff2") format("woff2"), url("../fonts/latolight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/latoregular.woff2") format("woff2"), url("../fonts/latoregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/latoblack.woff2") format("woff2"), url("../fonts/latoblack.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/latoheavy.woff2") format("woff2"), url("../fonts/latoheavy.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
body {
  font-size: 16px;
  font-family: "Lato", Arial, sans-serif;
  font-weight: 300;
  color: #333333;
  position: relative;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  position: relative;
  max-width: 1270px;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.head-h1 {
  font-size: 26px;
  line-height: 26px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  margin-top: 60px;
}
/*base end*/
.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 180px;
  padding: 0 15px;
}
.header__phone {
  display: flex;
  align-items: center;
}
.header__phone-link {
  color: #666666;
  font-size: 20px;
  margin-left: 10px;
}
.header__logo {
  margin: 0 20px;
}
.header__decor::after {
  content: url("../img/icon-img-logo.png");
  display: block;
  width: 325px;
  height: 367px;
  position: absolute;
  bottom: -700px;
  right: -30px;
  z-index: -1;
}
.header__decor--services::after {
  bottom: -510px;
  right: 0;
}
.header__decor--projectWork::after {
  left: 10px;
  bottom: 490px;
}
@media screen and (max-width: 730px) {
  .header {
    justify-content: center;
    padding: 20px 15px;
  }
  .header .button {
    margin-top: 10px;
  }
}
@media screen and (max-width: 535px) {
  .header__logo {
    margin: 20px;
  }
  .header .button {
    margin-top: 0;
  }
}
.button {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  color: #a59552;
  background-color: transparent;
  border: 1px solid #a59552;
  padding: 10px 20px;
  cursor: pointer;
  transition: 0.3s;
}
.button:hover {
  background-color: #0abbb5;
  border-color: #0abbb5;
  color: #ffffff;
}
.button--projectWork {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  padding: 10px 40px;
}
.buttonClosed {
  position: absolute;
  top: -11px;
  right: -11px;
  border: none;
  background: transparent;
  cursor: pointer;
  z-index: 5;
}
.nav {
  margin-right: 100px;
  margin-bottom: 40px;
  margin-left: 100px;
}
.nav__list {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  justify-content: space-between;
}
.nav__link {
  text-transform: uppercase;
  color: #333333;
  font-size: 18px;
  padding: 3px 5px;
  border-bottom: 1px solid transparent;
  transition: 0.6s;
}
.nav__link:hover {
  color: #a59552;
  border-bottom: 1px solid #0abbb5;
}
.nav__link--active {
  color: #a59552;
  border-bottom: 1px solid #0abbb5;
}
.nav__container {
  position: relative;
}
.nav__container:hover > .nav__services {
  display: block;
}
.nav__services {
  list-style-type: none;
  background: #ffffff;
  width: 330px;
  padding-bottom: 10px;
  padding-left: 30px;
  font-size: 16px;
  position: absolute;
  top: 24px;
  left: -25px;
  z-index: 5;
  display: none;
}
.nav__servicesLink {
  display: block;
  line-height: 24px;
  padding: 10px 0;
  color: #333333;
  transition: 0.4s;
}
.nav__servicesLink:hover {
  color: #0abbb5;
}
.slider {
  position: relative;
  background: rgba(129, 216, 208, 0.46);
  margin-bottom: 110px;
  font-size: 0;
  z-index: 5;
}
.slide {
  display: flex;
  align-items: center;
  justify-content: center;
}
.slideNext {
  display: none;
}
.slide__nav {
  align-self: stretch;
  padding: 40px;
  cursor: pointer;
  background: inherit;
  border: none;
  transition: 0.3s;
}
.slide__nav:hover {
  background: #81d8d0;
}
.slide__nav--forward {
  transform: scale(-1, 1);
}
.slider__controls {
  position: absolute;
  top: 490px;
  left: 50%;
  margin-left: -100px;
  display: flex;
}
.slider__controlsItem {
  width: 20px;
  height: 20px;
  border: 2px solid #0abbb5;
  border-radius: 50%;
  margin: 0 10px;
}
.slider__controlsItem--active {
  background: #0abbb5;
  transition: 0.3s;
}
.mainText {
  color: #333333;
  font-size: 18px;
  line-height: 28px;
  max-width: 750px;
  margin: 0 auto;
}
.sections {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 120px;
  margin-bottom: 80px;
  font-size: 0;
}
.sections__item {
  position: relative;
}
.sections__link {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  width: 400px;
  height: 280px;
  transition: 0.4s;
}
.sections__link:hover {
  background: rgba(129, 216, 208, 0.85);
}
.sections__link-text {
  font-size: 25px;
  font-weight: 800;
  text-transform: uppercase;
  margin-left: 10px;
}
.services {
  overflow: hidden;
}
.services__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}
.services__img {
  position: relative;
  background: rgba(129, 216, 208, 0.46);
  padding: 0 15px;
  font-size: 0;
}
.services__img::after {
  content: '';
  display: block;
  background: rgba(129, 216, 208, 0.46);
  width: 50vw;
  height: 210px;
  position: absolute;
  top: 0;
  right: 100%;
}
.services__img--right {
  order: 1;
}
.services__img--right::after {
  right: 0;
  left: 100%;
}
.services__name {
  margin-left: 50px;
  width: 380px;
}
.services__name--left {
  margin-left: 0;
  margin-right: 50px;
  text-align: right;
}
.services__head {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 30px;
  text-transform: uppercase;
}
.services__text {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 50px;
}
@media screen and (max-width: 790px) {
  .services__item {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
  }
  .services__name {
    width: auto;
    margin-left: 0;
    text-align: center;
  }
  .services__name .button {
    display: block;
    margin: 0 auto;
    width: 140px;
  }
  .services__img--right {
    order: 0;
  }
  .services__name--left {
    margin-right: 0;
  }
}
@media screen and (max-width: 510px) {
  .services__item,
  .services__text {
    padding: 0 20px;
  }
}
@media screen and (max-width: 400px) {
  .services__img::after {
    display: none;
  }
}
.portfolio {
  padding: 0 20px;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 -10px 10px -10px;
}
.portfolio__item {
  width: calc(33.3% - 20px);
  font-size: 0;
  margin: 0 10px 20px 10px;
  background: rgba(129, 216, 208, 0.46);
}
.portfolio__link:hover > .portfolio__img::after {
  opacity: 1;
}
.portfolio__img {
  position: relative;
}
.portfolio__img::after {
  content: '';
  width: 100%;
  height: 100%;
  border: 15px solid rgba(129, 216, 208, 0.8);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: 0.3s;
}
.portfolio__address {
  font-size: 18px;
  text-align: center;
  width: 240px;
  padding: 10px 0;
  margin: 0 auto;
}
@media screen and (max-width: 880px) {
  .portfolio__item {
    width: calc(50% - 20px);
  }
}
@media screen and (max-width: 580px) {
  .portfolio {
    justify-content: center;
  }
  .portfolio__item {
    width: 75%;
  }
}
@media screen and (max-width: 400px) {
  .portfolio__item {
    width: auto;
  }
}
.blog {
  padding-left: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 40px;
  margin-right: -10px;
  margin-left: -10px;
}
.blog__item {
  width: calc(33.3% - 20px);
  border: 1px solid #0abbb5;
  margin-right: 10px;
  margin-bottom: 30px;
  margin-left: 10px;
}
.blog__img {
  position: relative;
}
.blog__img::after {
  content: '';
  width: 100%;
  height: 100%;
  border: 15px solid rgba(129, 216, 208, 0.8);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
.blog__text {
  padding: 0 20px 10px 20px;
}
.blog__head {
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  text-align: center;
  margin-top: 22px;
}
.blog__description {
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 20px;
}
.blog__link {
  font-size: 14px;
  text-transform: uppercase;
  text-align: right;
  color: #a59552;
  display: block;
  padding: 10px 0 10px 10px;
  transition: 0.3s;
}
.blog__link:hover {
  color: #0abbb5;
}
@media screen and (max-width: 880px) {
  .blog {
    padding: 0 20px;
  }
  .blog__item {
    width: calc(50% - 20px);
  }
}
@media screen and (max-width: 580px) {
  .blog {
    justify-content: center;
  }
  .blog__item {
    width: 75%;
  }
}
@media screen and (max-width: 400px) {
  .blog__item {
    width: auto;
  }
}
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 60px;
  border: 1px solid #cccccc;
}
.pagination__wrap {
  display: flex;
  justify-content: center;
}
.pagination__arrow {
  color: #0abbb5;
}
.pagination__arrow--notActive {
  color: #cccccc;
  cursor: text;
}
.pagination__arrow,
.pagination__number {
  font-size: 16px;
  line-height: 16px;
  width: 50px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}
.pagination__number:hover {
  color: #ffffff;
  background: #0abbb5;
}
@media screen and (max-width: 480px) {
  .pagination a:nth-child(n+4):nth-child(-n+6) {
    display: none;
  }
}
.reviews {
  overflow: hidden;
}
.reviews__item {
  display: flex;
  justify-content: center;
  font-size: 0;
  padding-top: 60px;
  padding-bottom: 50px;
}
.reviews__img {
  position: relative;
  background: rgba(129, 216, 208, 0.46);
  padding: 0 15px;
  height: 140px;
}
.reviews__img::after {
  content: '';
  display: block;
  background: rgba(129, 216, 208, 0.46);
  width: 50vw;
  height: 140px;
  position: absolute;
  top: 0;
  right: 100%;
}
/*.reviews__item .reviews__img::after*/
.reviews__img--right::after {
  right: 0;
  left: 100%;
}
.reviews__imgItem {
  width: 180px;
  margin-top: -20px;
  position: relative;
  z-index: 5;
}
.reviews__author {
  width: 180px;
  text-align: center;
}
.reviews__name {
  font-size: 16px;
  line-height: 16px;
  font-weight: 600;
  display: block;
  margin-top: 25px;
  margin-bottom: 5px;
}
.reviews__contact {
  font-size: 14px;
  line-height: 14px;
}
.reviews__text {
  font-size: 16px;
  line-height: 20px;
  max-width: 485px;
  margin-top: -20px;
  margin-bottom: 0;
  margin-left: 80px;
  position: relative;
}
.reviews__text:after {
  content: url("../img/icon-quote.svg");
  display: block;
  position: absolute;
  top: -35px;
  left: -70px;
}
.reviews__text--left {
  order: -1;
  margin-left: 0;
  margin-right: 60px;
}
@media screen and (max-width: 800px) {
  .reviews {
    padding: 0 20px;
  }
  .reviews__item {
    flex-direction: column;
    align-items: center;
  }
  .reviews__text {
    margin-top: 20px;
    margin-left: 0;
  }
  .reviews__text--left {
    order: 1;
    margin-right: 0;
  }
}
.about {
  display: flex;
  justify-content: center;
  margin: 40px 0;
  padding: 0 20px;
}
.about__img {
  position: relative;
  font-size: 0;
  align-self: flex-start;
}
.about__img::after {
  content: '';
  display: block;
  background: rgba(129, 216, 208, 0.46);
  width: 50vw;
  height: 345px;
  position: absolute;
  top: 165px;
  right: 100%;
}
.about__img::before {
  content: '';
  width: 100%;
  height: 100%;
  border: 15px solid rgba(129, 216, 208, 0.8);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
.about__text {
  font-size: 18px;
  line-height: 28px;
  max-width: 480px;
  margin-top: 30px;
  margin-left: 40px;
}
@media screen and (max-width: 800px) {
  .about {
    flex-direction: column;
    align-items: center;
  }
  .about__img {
    align-self: auto;
  }
  .about__text {
    margin-left: 0;
    max-width: max-content;
  }
}
.projectWork {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}
.projectWork__img {
  position: relative;
  background: rgba(129, 216, 208, 0.46);
  padding: 0 15px;
  font-size: 0;
  align-self: flex-start;
}
.projectWork__img::after {
  content: '';
  display: block;
  background: rgba(129, 216, 208, 0.46);
  width: 50vw;
  height: 258px;
  position: absolute;
  top: 0;
  right: 100%;
}
.projectWork__listHead {
  margin-top: 0;
  margin-bottom: 30px;
  margin-left: 20px;
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  padding: 0 10px 3px 2px;
  border-bottom: 2px solid #0abbb5;
  display: inline-block;
}
.projectWork__list {
  list-style-type: none;
  padding-left: 0;
  padding-right: 20px;
  margin-top: 0;
  margin-left: 45px;
}
.projectWork__listItem {
  padding-left: 25px;
  font-size: 18px;
  line-height: 18px;
  margin-bottom: 10px;
  position: relative;
}
.projectWork__listItem::after {
  content: '';
  width: 7px;
  height: 7px;
  background: #0abbb5;
  border-radius: 50%;
  position: absolute;
  top: 6px;
  left: 0;
}
@media screen and (max-width: 720px) {
  .projectWork {
    flex-direction: column;
    align-items: center;
    padding: 0 20px;
  }
  .projectWork__list {
    margin-top: 30px;
    margin-bottom: 0;
    margin-left: 0;
    padding: 0;
  }
  .projectWork__img {
    align-self: auto;
  }
}
@media screen and (max-width: 450px) {
  .projectWork__img::after {
    display: none;
  }
}
.prices {
  display: flex;
  flex-wrap: wrap;
  font-size: 18px;
  margin: 50px 0;
}
.prices__item {
  width: 50%;
  border-collapse: collapse;
}
.prices__item td {
  padding-top: 7px;
}
.prices__price {
  text-align: center;
  color: #a59552;
  font-weight: 600;
}
.prices__area {
  padding-left: 100px;
}
.prices__head {
  background: rgba(129, 216, 208, 0.46);
}
.prices__headName {
  text-align: left;
  padding-top: 10px;
  padding-left: 100px;
  padding-bottom: 10px;
}
@media screen and (max-width: 760px) {
  .prices {
    margin-top: 0;
    margin-bottom: 20px;
  }
  .prices__item {
    width: 100%;
    margin-bottom: 30px;
  }
}
@media screen and (max-width: 450px) {
  .prices__head {
    padding: 0 20px;
  }
  .prices__headName {
    padding-left: 20px;
  }
  .prices__area {
    padding-left: 20px;
  }
}
.portfolioProject {
  display: flex;
  /*
  flex-wrap: wrap;*/
  margin: 50px 0;
  padding: 0 20px;
}
.portfolioProject__media {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.portfolioProject__text {
  width: calc(70% - 50px);
  margin-left: 50px;
  font-size: 18px;
  line-height: 28px;
}
.portfolioProject__text p {
  margin-top: 0;
}
@media screen and (max-width: 900px) {
  .portfolioProject {
    flex-direction: column;
  }
  .portfolioProject__media {
    width: auto;
  }
  .portfolioProject__text {
    width: auto;
    margin-top: 30px;
    margin-left: 0;
  }
}
.gallery {
  margin-bottom: 50px;
  font-size: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.gallery__main {
  margin-bottom: 5px;
  padding-bottom: 10px;
  border-bottom: 1px solid #0abbb5;
}
.gallery__items {
  display: flex;
  flex-wrap: wrap;
  align-self: center;
  margin-right: -5px;
  margin-left: -5px;
}
.gallery__item {
  width: calc(25% - 10px);
  margin: 5px;
}
.gallery__main,
.gallery__item {
  cursor: pointer;
}
@media screen and (max-width: 900px) {
  .gallery__items {
    margin: 0 auto;
  }
  .gallery__item {
    width: calc(12% - 10px);
  }
}
@media screen and (max-width: 650px) {
  .gallery__item {
    width: calc(17% - 10px);
  }
}
@media screen and (max-width: 500px) {
  .gallery__item {
    width: calc(25% - 10px);
  }
}
.blogPage {
  margin: 50px 0;
  padding: 0 40px;
}
.blogPage__img {
  display: block;
  margin: 0 auto;
}
.blogPage__text {
  font-size: 18px;
  line-height: 26px;
  margin: 15px 0;
}
.blogPage__h2 {
  font-size: 22px;
  font-weight: 600;
  line-height: 22px;
  margin: 20px 0;
}
.blogPage__h3 {
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  margin: 15px 0;
}
@media screen and (max-width: 678px) {
  .blogPage {
    padding: 0 15px;
  }
}
.footer {
  background: #666666;
}
.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding: 20px 15px;
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
}
.footer__socialLink a {
  margin-right: 10px;
}
.footer__contactsItem {
  display: flex;
  align-items: center;
  height: 35px;
}
.footer__contactsIcon {
  margin-right: 10px;
}
.footer__contacts p {
  line-height: 14px;
}
.footer__contacts a {
  color: #ffffff;
}
.footer__decor::after {
  content: url("../img/icon-img-logo.png");
  width: 325px;
  height: 367px;
  position: absolute;
  top: -590px;
  left: -160px;
  z-index: -1;
}
.footer__decor--blog::after {
  top: -380px;
}
@media screen and (max-width: 495px) {
  .footer__wrap {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .footer__socialLink {
    margin-top: 15px;
    margin-bottom: 5px;
  }
}
.modalRequest {
  width: 651px;
  padding: 40px 20px 30px 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  background-color: #ffffff;
  text-align: center;
  display: none;
}
.modalRequest::after {
  content: '';
  width: 100%;
  height: 100%;
  border: 15px solid rgba(129, 216, 208, 0.8);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
.modalHead {
  font-size: 28px;
  font-weight: 600;
  line-height: 28px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 10px;
}
.modalRequest__description {
  font-size: 18px;
  line-height: 18px;
  color: #a59552;
  margin-top: 0;
  margin-bottom: 30px;
}
.modalRequest__formWrap {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 30px;
}
.modalRequest__formWrap::after {
  content: "";
  background-image: url("../img/icon-img-logo.png");
  background-size: cover;
  width: 100px;
  height: 112px;
  position: absolute;
  top: 30px;
  left: 30px;
  z-index: 5;
}
.modalRequest__formWrap::before {
  content: "";
  background-image: url("../img/icon-img-logo.png");
  background-size: cover;
  width: 100px;
  height: 112px;
  position: absolute;
  bottom: 30px;
  right: 30px;
}
.modalRequest__formItem {
  margin: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.modalRequest__input {
  width: 255px;
  padding: 15px 0 15px 45px;
  margin-bottom: 20px;
}
.modalRequest__element {
  position: relative;
}
.modalRequest__element::after {
  content: url("../img/icon-user.svg");
  position: absolute;
  top: 19px;
  left: 15px;
}
.modalRequest__element--phone::after {
  content: url("../img/icon-phone-green.svg");
}
.modalRequest__element--mail::after {
  content: url("../img/icon-mail-green.svg");
  top: 17px;
}
.modalRequest__element--file::after {
  content: url("../img/icon-download.svg");
  top: 2px;
}
.modalRequest__file {
  display: block;
  padding-left: 45px;
  color: #a59552;
}
.modalRequest__labelText {
  display: block;
  margin-bottom: 10px;
}
.modalRequest__text {
  width: 255px;
  height: 167px;
  padding-top: 20px;
  padding-left: 20px;
  resize: none;
}
@media screen and (max-width: 680px) {
  .modalRequest {
    width: auto;
  }
  .modalRequest__formWrap::before {
    display: none;
  }
  .modalRequest__text,
  .modalRequest__labelText,
  .modalRequest__element--mail,
  .modalRequest__element--file,
  .modalRequest__file {
    display: none;
  }
}
.modalLuck {
  width: 590px;
  padding: 40px 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  background-color: #ffffff;
  text-align: center;
  display: none;
}
.modalLuck::after {
  content: '';
  width: 100%;
  height: 100%;
  border: 15px solid rgba(129, 216, 208, 0.8);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
.modalLuck__description {
  line-height: 16px;
}
.modalLuck__text {
  line-height: 20px;
}
.modalLuck__link {
  font-weight: 600;
  color: #a59552;
  cursor: pointer;
}
@media screen and (max-width: 600px) {
  .modalLuck {
    width: 300px;
  }
}
