.about {
  display: flex;
  justify-content: center;
  margin: 40px 0;
  padding: 0 20px;
}

.about__img {
  position: relative;
  font-size: 0;
  align-self: flex-start;

  &::after {
    content: '';
    display: block;
    background: @colorGreenLight;
    width: 50vw;
    height: 345px;
    position: absolute;
    top: 165px;
    right: 100%;
  }

  &::before {
    content: '';
    width: 100%;
    height: 100%;
    border: 15px solid rgba(129, 216, 208, 0.8);
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.about__text {
  font-size: 18px;
  line-height: 28px;
  max-width: 480px;
  margin-top: 30px;
  margin-left: 40px;
}

@media screen and (max-width: 800px) {
  .about {
    flex-direction: column;
    align-items: center;
  }

  .about__img {
    align-self: auto;
  }

  .about__text {
    margin-left: 0;
    max-width: max-content;
  }
}
