.footer {
  background: #666666;
}

.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding: 20px 15px;
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
}

.footer__socialLink a {
  margin-right: 10px;
}

.footer__contactsItem {
  display: flex;
  align-items: center;
  height: 35px;
}

.footer__contactsIcon {
  margin-right: 10px;
}

.footer__contacts p {
  line-height: 14px;
}

.footer__contacts a {
  color: #ffffff;
}

.footer__decor {
  &::after {
    content: url("../img/icon-img-logo.png");
    width: 325px;
    height: 367px;
    position: absolute;
    top: -590px;
    left: -160px;
    z-index: -1;
  }
}

.footer__decor--blog {
  &::after {
    top: -380px;
  }
}

@media screen and (max-width: 495px) {
  .footer__wrap {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .footer__socialLink {
    margin-top: 15px;
    margin-bottom: 5px;
  }
}
