.prices {
  display: flex;
  flex-wrap: wrap;
  font-size: 18px;
  margin: 50px 0;
}

.prices__item {
  width: 50%;
  border-collapse: collapse;
}

.prices__item td {
  padding-top: 7px;
}

.prices__price {
  text-align: center;
  color: #a59552;
  font-weight: 600;
}

.prices__area {
  padding-left: 100px;
}

.prices__head {
  background: @colorGreenLight;
}

.prices__headName {
  text-align: left;
  padding-top: 10px;
  padding-left: 100px;
  padding-bottom: 10px;
}

@media screen and (max-width: 760px) {
  .prices {
    margin-top: 0;
    margin-bottom: 20px;
  }

  .prices__item {
    width: 100%;
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 450px) {
  .prices__head {
    padding: 0 20px;
  }

  .prices__headName {
    padding-left: 20px;
  }

  .prices__area {
    padding-left: 20px;
  }
}
