.blogPage {
  margin: 50px 0;
  padding: 0 40px;
}

.blogPage__img {
  display: block;
  margin: 0 auto;
}

.blogPage__text {
  font-size: 18px;
  line-height: 26px;
  margin: 15px 0;
}

.blogPage__h2 {
  font-size: 22px;
  font-weight: 600;
  line-height: 22px;
  margin: 20px 0;
}

.blogPage__h3 {
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;
  margin: 15px 0;
}

@media screen and (max-width: 678px) {
  .blogPage {
    padding: 0 15px;
  }
}
